var milliseconds = 0;
var seconds = 0;
var minutes = 0;
var interval = undefined;
export function startTimer(html) {
    interval = setInterval(function () {
        milliseconds += 10;
        if (milliseconds === 1000) {
            milliseconds = 0;
            seconds++;
            if (seconds === 60) {
                seconds = 0;
                minutes++;
                if (minutes === 60) {
                    minutes = 0;
                }
            }
        }
        html.innerHTML = "".concat((minutes < 10 ? "0" + minutes : minutes) +
            "." +
            (seconds < 10 ? "0" + seconds : seconds));
    }, 10);
}
export function stopTimer() {
    clearInterval(interval);
    milliseconds = 0;
    seconds = 0;
    minutes = 0;
}
