// Рендер уровня сложности + выбор уровня сложности и запуск игры
function renderLevel() {
    var levelHtml = "<div class=\"top center\">\n    <div class=\"window\">\n        <h1 class=\"header\">\u0412\u044B\u0431\u0435\u0440\u0438 \u0441\u043B\u043E\u0436\u043D\u043E\u0441\u0442\u044C</h1>\n        <div class=\"cards\">\n            <button data-level=\"1\" class=\"card\">1</button>\n            <button data-level=\"2\" class=\"card\">2</button>\n            <button data-level=\"3\" class=\"card\">3</button>\n        </div>\n        <button class=\"button\">\u0421\u0442\u0430\u0440\u0442</button>\n    </div>\n    </div>";
    appEl.innerHTML = levelHtml;
    // при нажаите вызвать функцию с выбором сложности игры
    var levelButtons = document.querySelectorAll(".card");
    levelButtons.forEach(function (levelButton) {
        levelButton.addEventListener("click", function (event) {
            if (event.target instanceof HTMLElement) {
                var dataSet = event.target.dataset.level || "";
                numLevel = parseInt(dataSet);
                if (numLevel === 1) {
                    var numLevel_1 = 1;
                    console.log("Level :", numLevel_1);
                }
                else if (numLevel === 2) {
                    var numLevel_2 = 2;
                    console.log("Level :", numLevel_2);
                }
                else if (numLevel === 3) {
                    var numLevel_3 = 3;
                    console.log("Level :", numLevel_3);
                }
            }
        });
    });
    var startButton = document.querySelector(".button");
    startButton.addEventListener("click", function () {
        arr2 = [];
        start(numLevel, arr, arr2);
        stopTimer();
        renderGame(false);
        ints();
        console.log("Играть на уровне сложности :", numLevel);
    });
}
renderLevel();
