//Рендер игрового поля и повторный запуск игры
function renderGame(isAct) {
    var cardsHtml = arr2
        .map(function (back) {
        //рубашки карт
        return "<div id=\"".concat(back[0], "\"><img class=\"playingFieldCard back\" id=\"").concat(back[0], "\" data-name=\"").concat(back[1], "\" src=\"./cards/back.jpg\" alt=\"\"></div>");
    })
        .join("");
    var cardsBackHtml = arr2
        .map(function (back) {
        return "<div id=\"".concat(back[0], "\"><img class=\"playingFieldCard back\" id=\"").concat(back[0], "\" data-name=\"").concat(back[1], "\" src=\"./cards/").concat(back[1], ".jpg\" alt=\"\"></div>");
    })
        .join("");
    var gameHtml = "\n    <div class=\"playingField center\">\n        <div class=\"playingFieldHeader\">\n            <div class=\"timeBox\">\n                <div class=\"timeBoxHeader\">\n                    <p class=\"timeBoxHeader\">min</p>\n                    <p class=\"timeBoxHeader\">sec</p>\n                </div>\n                <div>\n                    <h1 class=\"time timerDisplay\">00.00</h1>\n                </div>\n            </div>\n            <div>\n                <button class=\"button returnButton\">\u041D\u0430\u0447\u0430\u0442\u044C \u0437\u0430\u043D\u043E\u0432\u043E</button>\n            </div>\n        </div>\n        <div id=\"css\" class=\"playingFieldCards\">\n            ".concat(isAct ? cardsHtml : cardsBackHtml, "\n        </div>\n    </div>\n    <div id=\"addHtml\" class=\"imgBg\"></div>");
    appEl.innerHTML = gameHtml;
    var css = document.getElementById("css");
    css.style.pointerEvents = "none";
    // Рендер результата
    function renderWin(isWin) {
        var _a;
        var time = (_a = document.querySelector(".timerDisplay")) === null || _a === void 0 ? void 0 : _a.innerHTML;
        var winHtml = "\n        <div class=\"img\"></div>\n        <div class=\"winLose center\">\n            <img class=\"imgHeader\" src=\"./cards/".concat(isWin ? "win.png" : "lose.png", "\" alt=\"winORlose\">\n            <h1 class=\"headerLow\">").concat(isWin ? "Вы выиграли!" : "Вы проиграли!", "</h1>\n            <h3 class=\"heading\">\u0417\u0430\u0442\u0440\u0430\u0447\u0435\u043D\u043D\u043E\u0435 \u0432\u0440\u0435\u043C\u044F:</h3>\n            <p class=\"timeHeader\">").concat(time, "</p>\n            <button style=\"margin-bottom: 48px\" class=\"button returnButton\">\u0418\u0433\u0440\u0430\u0442\u044C \u0441\u043D\u043E\u0432\u0430</button>\n        </div>");
        var addHtml = document.getElementById("addHtml");
        addHtml.innerHTML = winHtml;
        returnGame();
        stopTimer();
    }
    returnGame();
    //Поиск пары карт
    var playingFieldCards = document.querySelectorAll(".playingFieldCard");
    var compareNameId = [];
    var compareId = [];
    playingFieldCards.forEach(function (playingFieldCard) {
        playingFieldCard.addEventListener("click", function (event) {
            if (event.target instanceof HTMLElement) {
                var nameId = event.target.dataset.name || "";
                var id = event.target.id;
                playingFieldCard.setAttribute("src", "/cards/".concat(nameId, ".jpg"));
                compareId.push(id);
                compareNameId.push(nameId);
                if (compareId.length === 2 && compareNameId.length === 2) {
                    if (compareId[0] !== compareId[1]) {
                        if (compareNameId[0] === compareNameId[1]) {
                            score = score + 1;
                            step = ++step;
                            compareId = [];
                            compareNameId = [];
                        }
                        else {
                            step = ++step;
                            setTimeout(function () {
                                playingFieldCards.forEach(function (playingFieldCard) {
                                    playingFieldCard.setAttribute("src", "/cards/back.jpg");
                                });
                                console.log("Очистка");
                            }, 500);
                            compareId = [];
                            compareNameId = [];
                        }
                    }
                    else if (compareId[0] === compareId[1]) {
                        compareNameId.shift();
                        compareId.shift();
                    }
                }
                // Подсчет результатов
                console.log("Счет :", score, "Ход :", step);
                if (numLevel === 1) {
                    if (step === 5 || score === 3) {
                        if (score === 3) {
                            renderWin(true);
                        }
                        else if (score <= 3) {
                            renderWin(false);
                        }
                    }
                }
                else if (numLevel === 2) {
                    if (step === 8 || score === 6) {
                        if (score === 6) {
                            renderWin(true);
                        }
                        else {
                            renderWin(false);
                        }
                    }
                }
                else if (numLevel === 3) {
                    if (step === 9 || score === 9) {
                        if (score === 9) {
                            renderWin(true);
                        }
                        else {
                            renderWin(false);
                        }
                    }
                }
            }
        });
    });
}
// Показать/скрыть карточки через 5 сек в начале игры
function ints() {
    setTimeout(function () {
        renderGame(true);
        var timerRefs = document.querySelectorAll(".timerDisplay");
        timerRefs.forEach(function (timerRef) { return startTimer(timerRef); });
        var css = document.getElementById("css");
        css.style.pointerEvents = "auto";
        console.log("Старт");
    }, 5000);
}
